/* FormDemo.css */

.register-form-demo {
    margin-top: 80px;
}

.register-form-demo .card {
    min-width: 450px;
    padding: 25px;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.register-form-demo .card form {
    margin-top: 2rem;
}
.register-form-demo .card .field {
    margin-bottom: 1.5rem;
}
@media screen and (max-width: 960px) {
    .register-form-demo .card {
        width: 80%;
    }
}
@media screen and (max-width: 640px) {
    .register-form-demo .card {
        width: 100%;
        min-width: 0;
    }
}