.sidebar {
    background-color: #f8f8f8;
    overflow-y: auto;
    height: 90vh;
}

.sidebar::-webkit-scrollbar {
    width: 5px;
    border-radius: 5px;
}

.sidebar::-webkit-scrollbar:hover {
    width: 10px;
}

.sidebar::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.sidebar::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}

.sidebar::-webkit-scrollbar-thumb:hover {
    background: #555;
}
