body {
  margin: 0;
  overflow-y: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('./episteme-logo.png');
  background-repeat: no-repeat;
  background-size: 100% 160%;
}

body .p-sidebar {
  background: #f4f4f4;
  color: #333;
}

#postInput {
  width: 100%;
  margin-bottom: 10px;
}

#commentInput {
  width: 100%;
  margin-top: 10px;
}

.p-card {
  margin-bottom: 10px;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.primary {
  background-color: #053a3d;
  color: white;
}

.p-button {
  background-color: #053a3d;
}

.btn-primary {
  background-color: #053a3d !important;
}

.text-primary {
  color: #053a3d !important;
}

.primary:hover {
  background-color: #021c25;
  color: white;
}

.p-button:hover {
  background-color: #021c25;
}

.btn-primary:hover {
  background-color: #021c25 !important;
}

.text-primary:hover {
  color: #021c25 !important;
}

.text-primary-50 {
  color: white !important;
}

:root {
  --mdb-link-color: #053a3d !important;
  --mdb-primary-rgb: #053a3d !important;
  --mdb-info-rgb: #053a3d !important;
  --primary-400: #0d7f85 !important;
  --primary-700: #053a3d !important;
}